.map {
  max-width: 260px;
  stroke-width: 0.8;
}

.map-node--available {
  fill: white;
  stroke: #6991bd;
}

.map-node--available:hover,
.map-node--available:focus {
  fill: #dbe3eb;
}

.map-node--completed {
  fill: #9eb7d1;
  stroke: #587fa6;
}

.map-node--locked {
  stroke: #aeb8c2;
  fill: #ecf0f3;
}

.map-path--locked {
  stroke: #aeb8c2;
}

.map-path--completed,
.map-path--available {
  stroke: #9eb7d1;
}

.map-path {
  stroke-width: 1;
  fill: none;
}

/* Source: https://www.heropatterns.com/ */
.map-container {
  padding: 16px;
  text-align: center;
  background: #fff;
  background-image: url('/img/hero-patterns-topography.svg');
}
