p {
  font-size: 17px;
  line-height: 1.3em;
}

.center {
  text-align: center;
}

.secondary-text {
  color: var(--text_secondary);
}

.block-image {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  max-height: 50vh;
}

.action-layout__content {
  padding: 16px 18px 72px;
}

.action-layout__buttons {
  display: flex;
  box-sizing: border-box;
  /* width: 100%; */
  padding: 8px 16px 16px;
  background: #fff;
  box-shadow: 0px -20px 20px 0px #fff;
}

.action-layout__buttons > :not(:first-child) {
  margin-left: 8px;
}

.color-ribbon {
  display: flex;
  padding: 2px;
  background: #ebedf0;
  height: 10px;
  box-sizing: border-box;
}

.color-ribbon__item {
  height: 6px;
  width: 100%;
}

.color-ribbon__item:first-child {
  border-radius: 3px 0 0 3px;
}

.color-ribbon__item:last-child:not(:first-child) {
  border-radius: 0 3px 3px 0;
}

.color-ribbon__item--animated {
  animation: color-slide-in .4s ease-out forwards;
}

.color-ribbon__item--b {
  background: #2883ce;
}

.color-ribbon__item--r {
  background: #d8572b;
}

.color-ribbon__item--y {
  background: #d7bc00;
}

.color-ribbon__item--g {
  background: #69b200;
}

@keyframes color-slide-in {
  0% { width: 0; }
  100% { width: 100%; }
}

.ColorTiles__button {
  margin: 8px;
  display: inline-block;
  width: 100px;
  height: 100px;
  background-size: contain;
  border-radius: 4px;

  /* Reset default button styles */
  border: 0;
}

.ColorTiles__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
}

.ColorTiles__wrapper--inline {
  padding: 16px 8px;
}

.ColorTiles__button--inline {
  width: 18vw;
  height: 18vw;
  max-width: 128px;
  max-height: 128px;
}

.ColorTiles__container--inline {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.ColorTiles__container--inline > div {
  flex-shrink: 0;
}

.tile-directions {
  margin: 12vh auto 0;
  max-width: 232px;

  font-size: 1.25em;
  text-align: center;
}

.tile-container--init-transition,
.tile-container--turn-transition,
.tile-container--win-transition {
  /* https://easings.net/en#easeInOutSine */
  animation: container .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--init-transition .ColorTiles__button,
.tile-container--turn-transition .ColorTiles__button {
  animation: tile .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--init > div:last-child > .ColorTiles__button {
  opacity: 0;
}

.tile-container--init-transition > div:first-child > .ColorTiles__button {
  animation: init-visible-row-tile .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--init-transition > div:last-child > .ColorTiles__button {
  animation: init-hidden-row-tile .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--fade-out-transition {
  animation: fade-out .8s ease-in forwards;
}

.tile-container--win-transition .ColorTiles__button {
  animation: tile-win .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--hidden {
  display: none;
}

.tile-container--retry-init-transition {
  animation: fade-in .4s ease-in forwards;
  animation-delay: .4s;
  display: none;
}

.message-fade-in {
  animation: fade-in .2s ease-in forwards;
}

.message-fade-out {
  animation: fade-out .4s ease-in forwards;
}

@keyframes container {
  0% { transform: rotate(0deg); }
  50% { transform: rotate(180deg); }
  100% { transform: rotate(360deg); }
}

@keyframes tile {
  0% { transform: scale(1); border-radius: 0; }
  50% { transform: scale(0.6); border-radius: 50%; }
  100% { transform: scale(1); border-radius: 0; }
}

@keyframes tile-win {
  0% { transform: scale(1); border-radius: 0; }
  50% { transform: scale(0); border-radius: 50%; }
  100% { transform: scale(0); }
}

@keyframes init-visible-row-tile {
  0% { transform: scale(1); border-radius: 0; }
  50% { transform: scale(0); border-radius: 50%; }
  100% { transform: scale(1); border-radius: 0; }
}

@keyframes init-hidden-row-tile {
  0% { transform: scale(0); }
  50% { transform: scale(0); border-radius: 50%; opacity: 1; }
  100% { transform: scale(1); border-radius: 0; opacity: 1; }
}

@keyframes fade-out {
  0% { opacity: 1; display: block; }
  100% { opacity: 0; display: none; }
}

@keyframes fade-in {
  0% { opacity: 0; display: block; }
  100% { opacity: 1; display: block; }
}

.map {
  max-width: 260px;
  stroke-width: 0.8;
}

.map-node--available {
  fill: white;
  stroke: #6991bd;
}

.map-node--available:hover,
.map-node--available:focus {
  fill: #dbe3eb;
}

.map-node--completed {
  fill: #9eb7d1;
  stroke: #587fa6;
}

.map-node--locked {
  stroke: #aeb8c2;
  fill: #ecf0f3;
}

.map-path--locked {
  stroke: #aeb8c2;
}

.map-path--completed,
.map-path--available {
  stroke: #9eb7d1;
}

.map-path {
  stroke-width: 1;
  fill: none;
}

/* Source: https://www.heropatterns.com/ */
.map-container {
  padding: 16px;
  text-align: center;
  background: #fff;
  background-image: url('/img/hero-patterns-topography.svg');
}

.forest-button {
  color: #fff;
  font-size: calc(24px + 2vw);
  text-shadow: 0px 0px 8px #00000085;
}

.forest-popout-wrapper .ActionSheet {
  align-items: center;
  min-width: auto; /* Android-specific */
}

.forest-popout {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 16px;
  width: 228px;
  box-sizing: border-box;

  background-color: #fff;
  border-radius: 12px;
}

.forest-popout__letter {
  width: 80px;
  line-height: 80px;
  margin: 8px;
  z-index: 1; /* a hack for ActionSheet--android (not required for --ios) */

  font-size: 40px;
  text-align: center;
  background: #fff;
  border: 1px solid #9a9a9a;
  border-radius: 12px;
}

.forest-popout__letter:hover, .forest-popout__letter:focus {
  background: #eceaea;
}

.forest-letter-transition {
  animation: letter-transition .3s ease-in-out forwards;
}

@keyframes letter-transition {
  from { opacity: 0; }
  to { opacity: 1; }
}

